ul.wp-block-blocks-course-latest-post {
	list-style: none;
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	overflow: auto;
	gap: 10px;


	li {

		img {
			margin-bottom: 10px;
			width: 100%;
			height: auto;
			object-fit: cover;
		}

		h5 {
			font-size: 20px;
			margin: 0 0 5px;
			line-height: 1.4;

			a {
				color: #000;
			}
		}

		time {
			font-size: 12px;
			color: #969696;
		}

		p {
			font-size: 14px;
			color: #303030;
			line-height: 1.6;
			margin: 15px 0 0;
		}
	}
}